import React, { Component } from 'react';
import hoistNonReactStatic from 'hoist-non-react-statics';
import curry from 'curry';
import isEmpty from 'is-empty';

/* Rules should have the following format
 *  [
 *    {
 *      field: 'someField',
 *      condition: value => testFn(value),
 *      error: 'Some error message'
 *    }, {
 *      field: 'someOtherField',
 *      condition: (value, testObj) => value === testObj.someField,
 *      error: "Fields don't match",
 *    }
 *  ]
 */

function withValidation(rules, WrappedComponent) {
  function validate(obj) {
    const errors = rules.reduce((list, rule) => {
      if (!rule.condition(obj[rule.field], obj)) {
        if (list[rule.field] === undefined) {
          return { ...list, [rule.field]: [rule.error] };
        }

        return { ...list, [rule.field]: [...list[rule.field], rule.error] };
      }

      return list;
    }, {});

    return errors;
  }

  function isValid(obj) {
    return isEmpty(validate(obj));
  }

  class Validation extends Component {
    render() {
      return (
        <WrappedComponent
          {...this.props}
          validate={validate}
          isValid={isValid}
        />
      );
    }
  }

  Validation.displayName = `Validation(${getDisplayName(WrappedComponent)})`;

  hoistNonReactStatic(Validation, WrappedComponent);

  return Validation;
}

function getDisplayName(WrappedComponent) {
  return WrappedComponent.displayName || WrappedComponent.name || 'Component';
}

export default curry(withValidation);
